
import { defineComponent } from "vue";
import { useStore } from "@/store";
import { mapState, mapGetters, mapActions } from "vuex";
import { Notify } from "vant";
import { TransferBNB, TransferToken } from "@/erc20";
import axios from "@/axios";
import GoBack from "./GoBack.vue";

// TODO 修改充值收款钱包。接口从后端获取最好。
const ReceivePaymentAddress = "0x785D6a4613bBf5E82DEcf41bD6226867B54272D6";

export default defineComponent({
  components: { GoBack },
  name: "Home",
  data() {
    return {
      currentTokenIndex: 0,
      value: "",
      loading: false,
      supportTokens: [
        { label: "LBD", address: "0x48555E0Ef7DE6831a4fb63daE38F41cd7F8d0d9f" },
        // TODO Switch from test network to main network
        {
          label: "BNB",
          address: "BNB",
        },
      ],
    };
  },
  setup: () => {
    const { dispatch } = useStore();
    dispatch("init");
    return {};
  },
  computed: {
    ...mapState(["account", "activeChainId", "web3", "inviteCode"]),
    ...mapGetters(["isSupportChainId"]),
  },
  methods: {
    ...mapActions(["connectWallet"]),

    handleChangeToken(index: number) {
      this.currentTokenIndex = index;
    },
    async handleSubmit() {
      try {
        if (!this.isSupportChainId)
          return Notify({
            type: "danger",
            message: "不支持的网络，请切换钱包网络后重试",
          });
        if (!this.account)
          return Notify({ type: "danger", message: "请连接钱包后重试" });
        if (!(Number(this.value) > 100000000000))
          return Notify({ type: "danger", message: "充值数量必须大于1000亿" });

        this.loading = true;
        const token = this.supportTokens[this.currentTokenIndex];
        if (token.address === "BNB") {
          // BNB和其他代币转账方法不同
          const result = await TransferBNB(
            this.web3,
            this.account,
            ReceivePaymentAddress,
            this.value
          );
          console.log(result);
          // 充值/转账成功后获取到交易HASH值。可以将HASH传给后端，后端通过接口验证该笔交易和转账数量。验证成功后入库，增加用户金额。
          const transactionHash = result.transactionHash;
          console.log(transactionHash);
          //充值成功，调用积分接口
          axios
            .post("/auth/recharge", {
              wallet: this.account,
              point: this.value,
              inviteCode: this.inviteCode,
              hash: transactionHash,
            })
            .then((res) => {
              console.log("抽奖成功", res);
            });
        } else {
          const result = await TransferToken(
            this.web3,
            ReceivePaymentAddress,
            this.account,
            token.address,
            this.value
          );
          console.log(result);
          // 同转账BNB一样，成功后获取到交易HASH值。传给后端验证。验证方式与BNB有点不同。 在Node JS服务端DEMO中演示验证代码。
          const transactionHash = result.transactionHash;
          console.log(transactionHash);
          Notify({ type: "success", message: `${token.label}充值成功` });
          //充值成功，调用积分接口
          axios
            .post("/auth/recharge", {
              wallet: this.account,
              point: this.value,
              inviteCode: this.inviteCode,
              hash: transactionHash,
            })
            .then((res) => {
              console.log("抽奖成功", res);
            });
        }
      } catch (err) {
        console.error(err);
        Notify({ type: "danger", message: err.message });
      } finally {
        this.value = "";
        this.loading = false;
      }
    },
  },
});
