
import { computed, defineComponent } from "vue";
import { mapState, mapGetters } from "vuex";
import { Notify } from "vant";
import axios from "@/axios";
import LuckDraw from "./components/LuckDraw.vue";
import PrizeList from "./components/PrizeList.vue";
import InviteList from "./components/InviteList.vue";
import Loading from "../Loading.vue";
import InviteFriend from "./components/InviteFriend.vue";
import { useStore } from "@/store";
import NetError from "./components/NetError.vue";

export default defineComponent({
  components: {
    LuckDraw,
    PrizeList,
    InviteList,
    Loading,
    InviteFriend,
    NetError,
  },
  name: "Index",
  setup: () => {
    const store = useStore();
    store.dispatch("init");
    return {};
  },
  data() {
    return {
      inviteModel: false, //邀请好友
    };
  },
  computed: {
    ...mapState({
      user: "user",
      isfailed: "isfailed",
      account: "account",
    }),
    ...mapGetters(["isSupportChainId"]),
  },
  watch: {},
  methods: {
    //复制
    copy() {
      var input = document.createElement("input");
      input.value =
        "https://games.luckybabydoge.com?inviteCode=" + this.user.inviteCode;
      document.body.appendChild(input);
      input.select();
      document.execCommand("Copy");
      Notify({ type: "success", message: "复制成功" });
      document.body.removeChild(input); //复制完移除元素
    },
    invite() {
      this.inviteModel = true;
    },
    closeInvite() {
      this.inviteModel = false;
    },
  },
});
